.search-block-container {
  border: 1px solid;
  border-radius: 10px;
  padding: 10px;
  background: #ffffff;
  width: 75%;
  margin: 0 auto;
  margin-bottom: 10px;
  opacity: 0;
  transform: translateY(-20px);
  transition: opacity 0.5s ease, transform 0.5s ease;
}

.search-block-container.visible {
  opacity: 1;
  transform: translateY(0);
}

.search-block-list {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 10px;
}

.search-block-item {
  display: inline-block;
  vertical-align: top;
  width: 100%;
  overflow: hidden;
  opacity: 0;
  transition: opacity 0.5s ease;
}

.search-block-item:first-child {
  opacity: 1;
}

.search-block-item.visible {
  opacity: 1;
}

.search-block-select {
  transition: .3s;
  border: 1px solid #000;
  border-radius: 6px !important;
  padding: 13px 22px !important;
  min-height: 50px;
  line-height: 0;
  width: 100%;
  background-color: #20293a;
  color: #ffffff;
  font-weight: 100;
  font-size: 20px;
  height: 55px;
}

.search-btn {
  display: inline-flex;
  align-items: center;
  padding: 8px 12px;
  font-size: 16px;
  cursor: pointer;
  background-color: #3498db;
  color: #ffffff;
  border: none;
  border-radius: 4px;
  width: 75%;
  justify-content: center;
  margin-bottom: 10px;
}


.search-icon {
  margin-right: 8px;
}

